<template>
    <div class="home_contain">
        <el-row class="tac">
            <el-col :span="4" class="tac" style="background-color: #324057;">
                <el-menu class="tac" :default-active="defaultActive" background-color="#324057" text-color="#fff" active-text-color="#ffd04b" router>
                    <el-menu-item index="runkids.io"><i class="el-icon-menu"></i>首頁</el-menu-item>
                    <el-submenu index="1">
                        <template slot="title"><i class="el-icon-setting"></i><span>設定</span></template>
                        <el-menu-item index="user">個人資訊</el-menu-item>
                        <el-menu-item index="1-2">DEMO2</el-menu-item>
                        <el-menu-item index="1-3">DEMO3</el-menu-item>
                        <el-submenu index="1-4">
                            <template slot="title">DEMO4</template>
                            <el-menu-item index="1-4-1">DEMO4-1</el-menu-item>
                        </el-submenu>
                    </el-submenu>
                </el-menu>
            </el-col>
            <el-col :span="20">
                <Head></Head>
            </el-col>
            <el-col :span="20">
                <keep-alive>
                    <router-view class="showContain" />
                </keep-alive>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import Head from '@/components/Head'
import {mapGetters,mapActions} from 'vuex'
export default {
    components:{
        Head,
    },
    data(){
        return {
            userInfo : {}
        }
    },
    methods:{
        ...mapActions(['getUserInfo'])
    },
    computed: {
        defaultActive: function(){
            return this.$route.path.replace('/', '');
        }
    },
}
</script>
<style>
.home_contain{
    width: 100%;
    height: 100%;
}
.tac{
    height: 100%;
}
.showContain{
    height: 100%;
    overflow:auto;
}
</style>




